import React from 'react'
import s from './FormControl.module.css'
import {Field} from "redux-form";



const FormControl = ({input, meta, ...props}) => {
	return (
		<div className={s.form_control + ' ' + (meta.touched && meta.error && s.error)}>
			{props.children}
			{meta.touched && meta.error && <span>{meta.error}</span>}
		</div>
		)
}

export const Textarea = (props) => {
 const {input, meta, ...restProps} = props
	return <FormControl {...props}><textarea {...input} {...restProps} /></FormControl>
}

export const Input = (props) => {
	const {input, meta, ...restProps} = props
	return <FormControl {...props}><input {...input} {...restProps} /></FormControl>
}


export const createForm = (placeholder, name, validators, component, props ={}, text = '') => (
  <div>
    <Field placeholder={placeholder} name={name} validators={validators} component={component} {...props} /> {text}
  </div>
)