import {usersAPI} from './../API/api.js'
import {updateObjectInArray} from "../utils/object_helper";

const FOLLOW = 'FOLLOW';
const UNFOLLOW = 'UNFOLLOW';
const SET_USERS = 'SET_USERS';
const SET_CURRENT_PAGE = 'SET_CURRENT_PAGE';
const SET_USERS_TOTAL_COUNT = 'SET_USERS_TOTAL_COUNT';
const TOGGLE_IS_FETCHING = 'TOGGLE_IS_FETCHING';
const TOGGLE_IS_FOLLOWING_PROGRESS = 'TOGGLE_IS_FOLLOWING_PROGRESS';


let initialState = {
  usersData: [],
  pageSize: 12,
  totalUsersCount: 0,
  currentPage: 1,
  isFetching: true,
  followingIsProgress: []
}


const usersReduser = (state = initialState, action) => {
  switch (action.type) {
    case FOLLOW :
      return {
        ...state,
        usersData: updateObjectInArray(state.usersData, action.userId, 'id', {followed: true})
      }
    case UNFOLLOW :
      return {
        ...state,
        usersData: updateObjectInArray(state.usersData, action.userId, 'id', {followed: false})
      }
    case SET_USERS:
      return {...state, usersData: action.usersData}
    case SET_CURRENT_PAGE:
      return {...state, currentPage: action.currentPage}
    case SET_USERS_TOTAL_COUNT:
      return {...state, totalUsersCount: action.totalUsersCount}
    case TOGGLE_IS_FETCHING:
      return {...state, isFetching: action.isFetching}
    case TOGGLE_IS_FOLLOWING_PROGRESS:
      return {
        ...state, followingIsProgress: action.followingIsProgress
          ? [...state.followingIsProgress, action.userId]
          : state.followingIsProgress.filter(id => id !== action.userId)
      }
    default:
      return state;
  }
}


export const followSuccess = (userId) => ({type: FOLLOW, userId})

export const unfollowSuccess = (userId) => ({type: UNFOLLOW, userId})

export const setUsers = (usersData) => ({type: SET_USERS, usersData})

export const setCurrentPage = (currentPage) => ({type: SET_CURRENT_PAGE, currentPage})

export const setTotalUsersCount = (totalUsersCount) => ({type: SET_USERS_TOTAL_COUNT, totalUsersCount})

export const toggleIsFetching = (isFetching) => ({type: TOGGLE_IS_FETCHING, isFetching})

export const toggleIsFollowingProgress = (followingIsProgress, userId) => ({
  type: TOGGLE_IS_FOLLOWING_PROGRESS,
  followingIsProgress,
  userId
})


export const getUsers = (currentPage, pageSize) => {
  return async (dispatch) => {
    dispatch(toggleIsFetching(true));
    dispatch(setCurrentPage(currentPage));
    let data = await usersAPI.getUsers(currentPage, pageSize);
    dispatch(toggleIsFetching(false));
    dispatch(setUsers(data.items));
    dispatch(setTotalUsersCount(data.totalCount));
  }
}

const FollowUnfollowFlow = async (dispatch, userId, apiMethod, actionCreator) => {
  dispatch(toggleIsFollowingProgress(true, userId));
  let data = await apiMethod;
  if (data.resultCode === 0) {
    dispatch(actionCreator);
  }
  dispatch(toggleIsFollowingProgress(false, userId));
}

export const follow = (userId) => {
  return async (dispatch) => {
    FollowUnfollowFlow(dispatch, userId, usersAPI.follow(userId), followSuccess(userId))
  }
}

export const unfollow = (userId) => {
  return async (dispatch) => {
    FollowUnfollowFlow(dispatch, userId, usersAPI.unfollow(userId), unfollowSuccess(userId))
  }
}


export default usersReduser;