import React from 'react';
import s from './../CSS/post_element.module.css';

const Post_element=(props)=> {
	return (
		<main className={s.case}>
	        <div className={s.post_avatar}>
	          <img src="https://myduolife.com/images/img-profile.png"/>
	        </div>
	        <div className={s.post_txt}>
	        	<p>{props.msg}</p>
	        </div>
	        <div className={s.likes}>
	        	<span>{props.likes + ' likes'}</span>
	        </div>
    	</main>
	);
}

export default Post_element;