import React from "react";
import {createForm, Input, Textarea} from "../commons/FormControls";
import {reduxForm} from "redux-form";
import e from "../commons/FormControl.module.css";
import s from './../CSS/profile.module.css';


const ProfileDataForm = ({handleSubmit, profile, error}) => {
  return <form onSubmit={handleSubmit} className={s.prof_content}>
    {error && <span className={e.summary_error}>{error}</span>}
    <div>
      <button>Save</button>
    </div>
    <div className={s.main_info}>
      <p>fullName: {createForm('full name', 'fullName', [], Input)}</p>
      <p>Job: {createForm('Job', 'lookingForAJob', [], Input, {type: 'checkbox'})}</p>
      <p>Professional skills: {createForm('Professional skills', 'lookingForAJobDescription', [], Textarea)}</p>
      <p>About me: {createForm('About me', 'aboutMe', [], Textarea)}</p>
    </div>
    <div className={s.contacts}>
      <p>Contacts: {Object.keys(profile.contacts).map(key => {
        return <div key={key}>{key}: {createForm(key, 'contacts.' + key, [], Input)}</div>
      })}</p>
    </div>
  </form>
}

const ProfileDataFormReduxForm = reduxForm({form: 'edit-profile'})(ProfileDataForm)
export default ProfileDataFormReduxForm