import React from 'react';
import Main from './Main';
import {connect} from 'react-redux';
import {getUserProfile, getStatus, updateStatus} from './../Rudux/profile_reducer.js';
import {withRouter} from 'react-router-dom';
import {compose} from 'redux'
import {savePhoto, saveProfile} from "../Rudux/profile_reducer";


class ProfileContainer extends React.Component {

  refreshProfile() {
    let userId = this.props.match.params.userId;
    if (!userId) {
      userId = this.props.authorizedUser;
      if (!userId) {
        this.props.history.push("/login");
      }
    }
    this.props.getUserProfile(userId);
    this.props.getStatus(userId);
  }

	componentDidMount() {
    this.refreshProfile()
	}

  componentDidUpdate(prevProps, prevState, snapshot) {
    if (this.props.match.params.userId != prevProps.match.params.userId ) {
      this.refreshProfile();
    }
  }


	render() {
		return (
			<Main {...this.props} profile={this.props.profile}
            status={this.props.status}
            updateStatus={this.props.updateStatus}
      isOwner={!this.props.match.params.userId}
      savePhoto={this.props.savePhoto}
            saveProfile={this.props.saveProfile}/>
		)
	}
}


let mapStateToProps = (state) => {
	return {
		profile: state.profilePage.profile,
		status: state.profilePage.status,
		authorizedUser: state.auth.id,
		isAuth: state.auth.isAuth
	}
}


export default compose(
	connect(mapStateToProps, {getUserProfile, getStatus, updateStatus, savePhoto, saveProfile}),
	withRouter,
	)(ProfileContainer)