import React from 'react';
import {
  unfollow, follow, setCurrentPage, setTotalUsersCount,
  toggleIsFollowingProgress, getUsers
} from './../Rudux/users_reducer.js';
import {connect} from 'react-redux';
import Users from './Users.jsx';
import preloader from './../Assets/25.svg';
import {compose} from 'redux'
import {
  getUsersData, getPageSize, getFollowingIsProgress,
  getIsFetching, getCurrentPage, getTotalUsersCount
} from './../Rudux/users_selector.js'


class UsersAPIComponent extends React.Component {


  componentDidMount() {
    this.props.getUsers(this.props.currentPage, this.props.pageSize);
  }

  onPageChanged = (currentPage) => {
    this.props.getUsers(currentPage, this.props.pageSize);
  }

  render() {

    return (
      <div>
        {this.props.isFetching ? <img src={preloader}/> : null}
        <Users totalUsersCount={this.props.totalUsersCount}
               pageSize={this.props.pageSize} currentPage={this.props.currentPage}
               onPageChanged={this.onPageChanged}
               usersData={this.props.usersData} unfollow={this.props.unfollow}
               toggleIsFollowingProgress={this.props.toggleIsFollowingProgress}
               followingIsProgress={this.props.followingIsProgress} follow={this.props.follow}/>
      </div>
    )
  }
}


let mapStateToProps = (state) => {
  return {
    usersData: getUsersData(state),
    pageSize: getPageSize(state),
    totalUsersCount: getTotalUsersCount(state),
    currentPage: getCurrentPage(state),
    isFetching: getIsFetching(state),
    followingIsProgress: getFollowingIsProgress(state)
  }
}


export default compose(
  connect(mapStateToProps, {
    follow, unfollow,
    setCurrentPage, setTotalUsersCount, toggleIsFollowingProgress, getUsers
  })
)(UsersAPIComponent)