import React from 'react';
import s from './../CSS/users.module.css';
import UsersPagination from "../commons/UsersPagination";
import User from "./User";


const Users = (props) => {

  return (
    <div>
      <UsersPagination totalUsersCount={props.totalUsersCount} pageSize={props.pageSize}
                       currentPage={props.currentPage} onPageChanged={props.onPageChanged}/>
      <div className={s.user_case}>
        {props.usersData.map(u => <User key={u.id} user={u} follow={props.follow}
                                        followingIsProgress={props.followingIsProgress} unfollow={props.unfollow} />
        )}
      </div>
    </div>
  )
}


export default Users;