import React, {useState} from 'react';
import s from './../CSS/profile.module.css';
import preloader from './../Assets/25.svg';
import ProfileStatusWithHooks from './ProfileStatusWithHooks.jsx'
import withoutPhoto from './../Assets/img_profile.png'
import ProfileDataForm from "./ProfileDataForm";

const Profile = ({profile, status, updateStatus, isOwner, savePhoto, saveProfile}) => {

  let [editMode, setEditMode] = useState(false)

  const onMainPhotoSelected = (e) => {
    if (e.target.files.length) {
      savePhoto(e.target.files[0])
    }
  }

  const onSubmit = (formData) => {
    saveProfile(formData).then(
      () => {
        setEditMode(false)
      }
    )
  }

  if (!profile) {
    return <img src={preloader}/>
  }
  return (
    <main className={s.content}>
      <div className={s.prof_icon}>
        <img src={profile.photos.large != null ? profile.photos.large : withoutPhoto}/>
        {isOwner && <div className={s.select_img_button}>
          <label for={'file'}>Choose your new profile photo</label>
          <input id={'file'} name={'file'} type={"file"} onChange={onMainPhotoSelected}/>
        </div>}
      </div>
      <div className={s.prof_txt}>
        <div className={s.full_name}><h1>{profile.fullName}</h1></div>
        <div className={s.stat}><ProfileStatusWithHooks status={status} updateStatus={updateStatus}/></div>
        {editMode
          ? <ProfileDataForm initialValues={profile} profile={profile} onSubmit={onSubmit}/>
          : <ProfileData goToEditMode={() => {
            setEditMode(true)
          }} profile={profile} isOwner={isOwner}/>}
      </div>
    </main>
  );
}

const ProfileData = ({profile, isOwner, goToEditMode}) => {
  return <div className={s.prof_content}>
    <div>{isOwner && <button className={s.edit_btn} onClick={goToEditMode}>Edit your profile information</button>}</div>
    <div className={s.main_info}>
      <p><label>fullName:</label> {profile.fullName}</p>
      <p><label>Job:</label> {profile.lookingForAJob ? 'search' : 'no'}</p>
      <p><label>Professional skills:</label> {profile.lookingForAJobDescription}</p>
      <p><label>About me:</label> {profile.aboutMe}</p>
    </div>
    <div className={s.contacts}>
      <div><h3>Contacts:</h3> {Object.keys(profile.contacts).map(key => {
        return <Contacts key={key} contactTitle={key} contactValue={profile.contacts[key]}/>
      })}</div>
    </div>
  </div>
}


const Contacts = ({contactTitle, contactValue}) => {
  return <div><label>{contactTitle}: </label><a href={'contactValue'}>{contactValue}</a></div>
}


export default Profile;