const ADD_MESSAGE = 'ADD-MESSAGE';


let initialState = {
	messagesData: [
		{id: 1, msg: 'Hi, how are you!s?'},
		{id: 2, msg: 'Hi! Iam fine. Wat about you?'},
		{id: 3, msg: 'Note bad...'}
	],

	dialogsData: [
		{id: 1, name: 'Dmitrii'},
		{id: 2, name: 'Mikel'},
		{id: 3, name: 'Sasha'},
		{id: 4, name: 'Nikita'},
		{id: 5, name: 'Valera'}
	]
}




const dialogsReducer = (state = initialState, action) => {
	switch (action.type) {
		case ADD_MESSAGE:
			let newMsg = {id: 4, msg: action.newMessageBody};

			return {...state, 
				messagesData: [...state.messagesData, newMsg]
			};
		default:
			return state;
	}
}


export const addMessageActionCreator = (newMessageBody) => {
	return {
		type: ADD_MESSAGE,
		newMessageBody
	}
}


export default dialogsReducer;