import React, {useState, useEffect} from 'react';
import s from './../CSS/profile.module.css';

const ProfileStatusWithHooks = (props) => {

	let [editMode, setEditMode] = useState(false)
	let [status, setStatus] = useState(props.status)
// проводится деструктаризация и присваивания значениям массива, который возвращает useState, переменных

	useEffect( () => {
		setStatus(props.status)
	}, [props.status])

	const activateEditMode = () => {
		setEditMode(true)
	}
	const deactivateEditMode = () => {
		setEditMode(false)
		props.updateStatus(status)
	}
	const onStatusChange = (e) => {
		setStatus(e.currentTarget.value)
	}

		return (
      <main><label className={s.stat_label}>Status: </label>
				{!editMode && <label onDoubleClick={activateEditMode}>{props.status || 'no status'}</label>}
				{editMode && <input value={status} onChange={onStatusChange}
				 autoFocus={true} onBlur={deactivateEditMode}/>}
	    </main>
		)
	
}

export default ProfileStatusWithHooks;