import React from 'react';
import s from './../CSS/dialogs.module.css';
import {NavLink, Redirect} from 'react-router-dom';
import { Field, reduxForm } from 'redux-form'
import {required, maxLengthCreation} from './../utils/validators/validators.js'
import {Textarea} from './../commons/FormControls.js'

const DialogListener = (props) => {
	return (
		<div className={s.dialog_itm}>
			<NavLink to={'/dialogs/' + props.id}>{props.name}</NavLink>
		</div>
	);
}

const MessageItem = (props) => {
	return (
		<div className={s.message_itm}>
			<p>{props.msg}</p>
		</div>
	);
}

const maxLength10 = maxLengthCreation(10);

const AddNewMessageForm = (props) => {
	return (
		<form onSubmit={props.handleSubmit}>
			<Field name='newMessageBody' component={Textarea} type='text' validate={[required, maxLength10]} />
			<button type='submit'>Send</button>
		</form>
		)
}

const AddMessagReduxForm = reduxForm({form: 'addMessageBody'})(AddNewMessageForm)




const Dialogs=(props)=> {
	let state = props.messagesPage;

	let dialogsElement = state.dialogsData.map(dialog => <DialogListener name={dialog.name} id={dialog.id} />);
	let messagesElement = state.messagesData.map(message => <MessageItem msg={message.msg} />);

	let newMessageElement = React.createRef();

	let addMessage = (values) => {
		props.addMessage(values.newMessageBody);
	}


	return (
		<div className={s.dialogs}>
			<div className={s.dialogs_list}>
				{dialogsElement}
			</div>
			<div className={s.messages_wrap}>
				{messagesElement}
				<div className={s.new_message_case}>
					<AddMessagReduxForm onSubmit={addMessage} />
				</div>
			</div>
		</div>
	);
}

export default Dialogs;