import React from 'react';
import {addMessageActionCreator, apdateNewMessageTextActionCreator} from './../Rudux/dialogs_reducer.js';
import Dialogs from './Dialogs.jsx';
import {connect} from 'react-redux';
import {NavLink, Redirect} from 'react-router-dom';
import {withAuthRiderect} from './../hoc/withAuthRedirect.js'
import {compose} from 'redux'



let mapStateToProps = (state) => {
	return {
		messagesPage: state.messagesPage
	}
}

let mapDicpatchToProps = (dispatch) => {
	return {
		addMessage: (newMessageBody) => {
			dispatch(addMessageActionCreator(newMessageBody));
		}
	}
}


export default compose(
	connect(mapStateToProps, mapDicpatchToProps),
	withAuthRiderect
	)(Dialogs)