import {getAuthUserData} from './auth_reducer.js'

const INITIALIZADE_SACCESS = 'INITIALIZADE_SACCESS';

let initialState = {
	initializade: false
}


const appReducer = (state = initialState, action) => {
	switch (action.type) {
		case INITIALIZADE_SACCESS:
			return {...state, initializade: true};
		default:
			return state;
	}
}


export const initializadeSaccess = () => ({type: INITIALIZADE_SACCESS})


export const initializeApp = () => (dispatch) => {
	let promise = dispatch(getAuthUserData())
	Promise.all([promise])
	.then(() => {
			dispatch(initializadeSaccess())
		})
}


export default appReducer;