import React from 'react';
import {Field, reduxForm} from 'redux-form'
import {required, maxLengthCreation} from './../utils/validators/validators.js'
import {Input} from './../commons/FormControls.js'
import {connect} from 'react-redux'
import {login} from './../Rudux/auth_reducer.js'
import {Redirect} from 'react-router-dom'
import s from './../commons/FormControl.module.css'


const LoginForm = ({handleSubmit, error}) => {
  return (
    <form onSubmit={handleSubmit}>
      <Field name="email" component={Input} type="text" placeholder='email' validate={[required]}/>
      <Field name="password" component={Input} type="password" placeholder='Password' validate={[required]}/>
      <Field name="rememberMe" component="input" type="checkbox"/> remember me
      <button type="submit">Login</button>
      {error && <span className={s.summary_error}>{error}</span>}
    </form>
  )
}

const LoginReduxForm = reduxForm({form: 'login'})(LoginForm)

const Login = (props) => {
  const onSubmit = (formData) => {
    props.login(formData.email, formData.password, formData.rememberMe)
  }

  if (props.isAuth) {
    return <Redirect to={'/profile'}/>
  }

  return (
    <div>
      <h1>Login</h1>
      <LoginReduxForm onSubmit={onSubmit}/>
    </div>
  )
}

const mapStateToProps = (state) => ({
  isAuth: state.auth.isAuth
})

export default connect(mapStateToProps, {login})(Login);