import React from 'react';
import Profile from './Profile.jsx';
import s from './../CSS/main.module.css';
import PostsContainer from './PostsContainer.jsx';

const Main=(props)=> {

	return (
		<main className={s.content}>
	        <div className={s.prev_img}>
	          <img src="https://interier-foto.ru/wp-content/uploads/dlinnye-foto-4.jpg"/>
	        </div>
     		<Profile profile={props.profile} status={props.status}
                 updateStatus={props.updateStatus}
                 isOwner={props.isOwner}
                 savePhoto={props.savePhoto}
                 saveProfile={props.saveProfile}/>
     		<PostsContainer />
    	</main>
	);
}

export default Main;