import React from 'react';
import Post_element from './Post_element.jsx';
import s from './../CSS/posts.module.css';
import { Field, reduxForm } from 'redux-form'
import {required, maxLengthCreation} from './../utils/validators/validators.js'
import {Textarea} from './../commons/FormControls.js'




const Posts=(props)=> {
	
	let postsElement = props.postsData.map (post => <Post_element key={post.id} msg={post.msg} likes={post.likes} />)
	
	let newPostElement = React.createRef();


	let onAddPost = (values) => {
		props.addPost(values.postText);
	}

	return (
		<div className={s.case}>
			<PostReduxForm onSubmit={onAddPost} />
			<div className={s.posts_case}>
				{postsElement}
			</div>
		</div>

	);
}


const maxLength10 = maxLengthCreation(10);

const PostForm = (props) => {
	return (
		<form onSubmit={props.handleSubmit}>
			<Field className={s.new_txt} component={Textarea} type='text' name='postText' validate={[required, maxLength10]}/>
			<button type="submit">Add post</button>
		</form>
		)
}

const PostReduxForm = reduxForm({form: 'newPost'})(PostForm)

export default Posts;