import React from 'react';
import s from './../CSS/users.module.css';
import {NavLink} from 'react-router-dom';


const User = ({user, followingIsProgress, unfollow, follow}) => {
  return (
      <div className={s.user_card}>
          <div className={s.user_icon}>
            <NavLink to={'/profile/' + user.id}>
              <img src={user.photos.small != null ? user.photos.small : 'https://myduolife.com/images/img-profile.png'}/>
            </NavLink>
          </div>
          <div className={s.user_txt}>
            <p>{user.name}</p>
            <p>{user.status}</p>
          </div>
          <div className={s.follow_btn}>
            {user.followed
              ? <button disabled={followingIsProgress.some(id => id === user.id)} onClick={() => {
                unfollow(user.id)

              }}>Unfollow</button>
              : <button disabled={followingIsProgress.some(id => id === user.id)} onClick={() => {
                follow(user.id)

              }}>Follow</button>}
          </div>
        </div>)}


export default User;