import React from 'react';
import './App.css';
import HeaderContainer from './Components/HeaderContainer';
import Navbar from './Components/Navbar';
import News from './Components/News';
import Music from './Components/Music';
import Settings from './Components/Settings';
import Login from './Components/Login.jsx';
import ProfileContainer from './Components/ProfileContainer.jsx';
import UsersContainer from './Components/UsersContainer.jsx';
import DialogsContainer from './Components/DialogsContainer.jsx';
import {BrowserRouter, Redirect, Route} from 'react-router-dom';
import {initializeApp} from './Rudux/app_reducer.js';
import {connect, Provider} from 'react-redux';
import preloader from './../src/Assets/25.svg'
import store from "./Rudux/redux_store";


class App extends React.Component {

  componentDidMount() {
    this.props.initializeApp()
  }

  render() {
    if (!this.props.initializade) {
      return <img src={preloader}/>
    }
    return (
      <div className='App'>
        <HeaderContainer/>
        <Navbar/>
        <div className='App_wrapper_content'>
          <Redirect exact from='/' to='/profile' />
          <Route path='/profile/:userId?' render={() => <ProfileContainer/>}/>
          <Route path='/dialogs' render={() => <DialogsContainer/>}/>
          <Route path='/users' render={() => <UsersContainer/>}/>
          <Route path='/news' render={() => <News/>}/>
          <Route path='/music' render={() => <Music/>}/>
          <Route path='/settings' render={() => <Settings/>}/>
          <Route path='/login' render={() => <Login/>}/>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  initializade: state.app.initializade
})

let AppContainer = connect(mapStateToProps, {initializeApp})(App);

let SocialApp = (props) => {
  return (
    <BrowserRouter>
      <Provider store={store}>
        <AppContainer />
      </Provider>
    </BrowserRouter>
      )
}

export default SocialApp;